	module.exports = {"title":"Office UI Fabric for iOS","date":"2017-02-14T08:49:36.108Z","layout":"post","draft":true,"place":"blog","tags":["Javascript","React","Microsoft Fabric"],"categories":["Front End development"],"coverPhoto":null,"body":"<p>Were thrilled to announce the release of <a href=\"https://github.com/OfficeDev/Office-UI-Fabric-iOS\">Office UI Fabric for iOS</a>. This is the first time weve brought the Office Design Language to a new platform. While its not yet as full-featured as our web toolkit, this initial release includes core styles to lay the foundation: colors, typography, and simple components.</p>\n<h3 id=\"typography\">Typography</h3>\n<blockquote>\n<p>Typography</p>\n</blockquote>\n<p>The type ramp is the same as we use on the web.\nNote: The screenshot does not include all of the sizes available.</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> react <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'react'</span>;\n<span class=\"hljs-comment\">// ComponentA.js</span>\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">default</span> {\n  <span class=\"hljs-attr\">template</span>: <span class=\"hljs-string\">'&lt;div&gt;{{ message }}&lt;/div&gt;'</span>,\n  data () {\n    <span class=\"hljs-keyword\">return</span> {\n      <span class=\"hljs-attr\">message</span>: <span class=\"hljs-string\">'Hello!'</span>\n    }\n  }\n}\n</code></pre>\n<h3 id=\"colors\">Colors</h3>\n<p>Weve started with the core palette of theme, neutral, and accent colors that youll find in Fabric for Web. In additional, weve included a hashing algorithm that returns a color for any given string.</p>\n<pre><code class=\"language-html\"><span class=\"hljs-comment\">&lt;!-- MyComponent.vue --&gt;</span>\n\n<span class=\"hljs-comment\">&lt;!-- css --&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">style</span>&gt;</span><span class=\"css\">\n<span class=\"hljs-selector-class\">.message</span> {\n  <span class=\"hljs-attribute\">color</span>: red;\n}\n</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">style</span>&gt;</span>\n\n<span class=\"hljs-comment\">&lt;!-- template --&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">template</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"message\"</span>&gt;</span>{{ message }}<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">template</span>&gt;</span>\n\n<span class=\"hljs-comment\">&lt;!-- js --&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">script</span>&gt;</span><span class=\"javascript\">\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">default</span> {\n  <span class=\"hljs-attr\">props</span>: [<span class=\"hljs-string\">'message'</span>],\n  created() {\n    <span class=\"hljs-built_in\">console</span>.log(<span class=\"hljs-string\">'MyComponent created!'</span>)\n  }\n}\n</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">script</span>&gt;</span>\n</code></pre>\n<h3 id=\"components\">Components</h3>\n<p>Two simple components, InitialsView and LogoView, are included. These can be used to represent people, groups, or sites.</p>\n<h3 id=\"ready-to-start\">Ready to start?</h3>\n<p>Swing by <a href=\"https://github.com/OfficeDev/Office-UI-Fabric-iOS\">the repository</a> to check out the readme and code. Well be rolling out controls that leverage these assets in the future, so check back for more updates!</p>\n"}

