	module.exports = {"title":"Fabric Localization","date":"2017-03-07T01:39:32.472Z","layout":"post","draft":true,"place":"blog","tags":["Javascript","Localization","Microsoft Fabric"],"categories":["Front End development"],"coverPhoto":null,"body":"<h2 id=\"right-to-left-layouts\">Right-to-left layouts</h2>\n<p>Fabric comes with an alternate CSS file for pages written in right-to-left (RTL) languages, such as Arabic and Hebrew. This reverses the order of columns in the responsive grid, making it easy to create an RTL layout without writing additional templates. Many icons are also reversed, particularly those used for navigation such as arrows.</p>\n<h3 id=\"implementation\">Implementation</h3>\n<p>Apply the dir attribute to the HTML tag, and reference the RTL version of Fabric.</p>\n<pre><code class=\"language-js\">&lt;html dir=<span class=\"hljs-string\">\"rtl\"</span>&gt;\n<span class=\"xml\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">head</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">meta</span> <span class=\"hljs-attr\">charset</span>=<span class=\"hljs-string\">\"utf-8\"</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">link</span> <span class=\"hljs-attr\">rel</span>=<span class=\"hljs-string\">\"stylesheet\"</span> <span class=\"hljs-attr\">href</span>=<span class=\"hljs-string\">\"fabric-[version].rtl.min.css\"</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">head</span>&gt;</span>\n...\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">html</span>&gt;</span>\n</span></code></pre>\n<h3 id=\"directional-icons\">Directional icons</h3>\n<p>With the reading direction set to RTL and Fabrics RTL stylesheet referenced (see above), directional icons will automatically be substituted. These pairs of icons will be swapped when viewed on RTL pages:</p>\n<h3 id=\"language-optimized-fonts\">Language-optimized fonts</h3>\n<p>By default, Fabric presents all text using the Western European character set of Segoe UI. For languages with other characters, Fabric will either serve a version of Segoe UI with a different character set or use a system font.</p>\n<h3 id=\"implementation-2\">Implementation</h3>\n<p>The HTML lang attribute specifies the language of the elements content. This is typically applied to the root HTML element, where it will be inherited by the entire page. In this example the entire page is in Thai.</p>\n<pre><code class=\"language-js\">&lt;html lang=\"th-TH\"&gt;...&lt;/html&gt;\n</code></pre>\n<p>For pages with content in multiple languages, the lang attribute can be applied to individual elements. In this example, a page that is mostly Thai also contains some Vietnamese.</p>\n<pre><code class=\"language-js\">&lt;html lang=\"th-TH\"&gt;\n...\n&lt;section lang=\"vi-VN\"&gt;...&lt;/section&gt;\n&lt;/html&gt;\n</code></pre>\n"}

